












export default {
	data() {
		return {
			bookData:[],
			bookid:'',
			userData:[]
		};
	},
	onLoad(e) {
		this.bookid = e.id;
		this.userData= uni.getStorageSync('user');
		this.getBookMeun();
	},
	methods:{
		//获取书籍目录
		getBookMeun() {
			uni.showLoading({
			  title: '加载中...'
			})
			var that = this,
					zhang = '',
					bid = that.bookid,
					userData = this.userData;
			if (userData.length) {
				userData.forEach(value => {
					if (value.chapId == bid) {
						zhang = value.zhang
					}
				})
			}
			uni.request({
			  url: 'https://novel.juhe.im/book-chapters/' + bid,
			  data: {},
			  header: {
				'content-type': 'application/json' // 默认值
			  },
			  method: 'GET',
			  success: function (res) {
				uni.hideLoading()
				var timeData = new Date(res.data.updated.replace("T", " ").replace("Z", "").replace(/-/g, '/').split('.')[0])
				var bookData = res.data.chapters;
				if (zhang > 1) {
					uni.showModal({
						title: '提示',
						content: '亲爱的，你上次读到第' + zhang + '章，是否继续阅读',
						success: function (res) {
							if (res.confirm) {
								let nowCap= zhang - 1;
								uni.navigateTo({
									url: '../bookcontenr/bookcontenr?idx=' +　nowCap + '&bid=' + bid
								});
							} else if (res.cancel) {
							 
							}
						},
						fail: function (res) { },
						complete: function (res) { },
					})
				}
				bookData.forEach((val,index)=>{
					val.reading = false;
					if(index==zhang-1){
						val.reading = true;
					}
				})
				that.bookData = bookData;
				that.maxCap= res.data.chapters.length-1;
				that.laiYuanName= res.data.name;
				that.newChap= res.data.chapters[res.data.chapters.length-1]
			  }
			})
		},
		//选择章节
		getDetails(e){
			var idx = e.target.dataset.idx,
					bid = this.bookid
			this.nowCap = idx;
			uni.navigateTo({
				url: '../bookcontenr/bookcontenr?idx=' + idx + '&bid=' + bid
			});
		}
	}
}



